import Vue from 'vue'
import Component from 'vue-class-component'
import HeadView from '../components/headView.js'
import InputView from '../components/inputView.js'
import './content.css'
import { searchHandle} from './methods.js'
import DetailAlertView from '../components/detailAlertView.js'

@Component
export default class HomePage extends Vue {
	wrapperHeight = 0

    searchResult = []
    showAlert = false
	created() {
    this.wrapperHeight = document.documentElement.clientHeight
   }

   handleSearch (value) {
      value = value.replace(/\s+/g,"");
      if (!value || value=="") {
        alert("请输入记录编号");
        return;
      }
       var p = searchHandle(value)

       var self = this
       p.then(function (rs) {
            self.searchResult = rs
            self.showAlert = true
        });
   }

   handleJumpAdd (){
   		this.$router.push({name: 'AddRecord'})
   }

   handlerGoManager (){
    this.$router.push({name:'ManagerRecord'})
   }

   handleShowAlert (){
        this.showAlert = false
   }
   get caculateHeight() {
    return 'height:' + String(this.wrapperHeight) + 'px'
  }

  get caculateScrollHeight() {
    return 'height:' + String(this.wrapperHeight - 64) + 'px'
  }
  render () {
    return (
     <div class = "mainContent" style ={this.caculateHeight}>
     	<HeadView onManagerRecord = {this.handlerGoManager} onAddRecord={this.handleJumpAdd}></HeadView>

     	<div class = "pageScroll" style={'overflow:auto;'+this.caculateScrollHeight} >

     		<div class = "titleClass">
     			<p style='font-size:38px'>公正簿</p>
     			<p>一个管理应收账款的记录簿</p>
     			<p>依托NAS生态，实现区块链真实落地</p>
     			<p>记录清晰，公正公平</p>
     		</div>

     		<InputView onSearch={value =>{this.handleSearch(value)}}> </InputView>
     		
     		<div style='position:relative;top:30px;height:1px;background:#999;width:80%;margin:0 10%'></div>

     		<dl class='processCSS'>
     			<dd>
     				流程说明
     			</dd>
     			<dt>
     			  <p>1.生成记录</p>
     			  <p style='margin-left:10px'>上游用户创建应付账款记录，指定下游用户某钱包地址为收款地址</p>
     			  <p>2.记录确认</p>
     			  <p style='margin-left:10px'>下游用户使用指定收款地址进行记录确认。此时记录生效</p>
     			  <p>3.记录变更（可多次变更）</p>
     			  <p style='margin-left:10px'>下游用户生成一条变更记录，将应收账款的所有权转让A用户，并将收款地址更改为A用户钱包地址</p>
     			  <p>4.记录完结</p>
     			  <p style='margin-left:10px'>上游用户将应付账款兑换为等价NAS,转账给最后一条变更记录的指定钱包地址</p>
     			  <p>5.完结确认</p>
     			  <p style='margin-left:10px'>收款地址用户进行收款确认</p>
     			</dt>
     		</dl>
     		<div style='position:relative;top:10px;height:1px;background:#999;width:80%;margin:0 10%'></div>

     		<dl class='processCSSRight'>
     			
     			<dt>
     			  <p>1.张老板给富士康供货价值100万的手机屏，富士康收下手机屏后，给张老板开一张100万元的应收账款票据，票据写明3个月后可凭该票据找富士康提现100万元</p>
     			  <p>2.张老板的手机屏厂接到其他订单，需要采购生成手机屏的原材料，但手上现金不足。于是张老板找到刘老板，拿出富士康的100万元应收账款票据，希望能换成95万元立马到手的现金，用于去采购原材料。3个月后富士康的100万元款项全归刘老板所有。</p>
     			  <p>3.刘老板觉得这事虽有赚头，但不清楚张老板手上的这个票据是否真实，是否在他处已质押过。于是告诉张老板，你给我【公证簿】上的记录编号。我需要查看你的应收账款完整记录。若票据真实，且目前收款地址属于你，那你新增一条变更记录。将应收账款所有权转给我并指定我的钱包地址为最终收款地址。我就愿意给你95万元现金。</p>
     			  <p>4.记录变更完成，3个月后，刘老板到富士康进行提现。富士康通过编号查看【公证簿】记录，多方确认后，将100万元货款兑换为等价的NAS，汇款给指定的NAS收款地址。</p>
     			  <p>5.刘老板进行收款确认。交易完结</p>
     			</dt>
     			<dd>
     				使用案例
     			</dd>
     		</dl>

     		 <div style='position:relative;top:0px;height:1px;background:#999;width:80%;margin:0 10%'></div>

     		 <div class='aboutUs'>
     		 	<p>关于我们</p>
     		 	<p>NAS Dapp开发者 ------致力推进区块链真实落地</p>
     		 	<p>联系我们： 469565173@qq.com</p>
     		 </div>
     	</div>
        <DetailAlertView items={this.searchResult} v-show={this.showAlert} onClose={this.handleShowAlert} ></DetailAlertView>

      </div>
    )
  }
}


