import Vue from 'vue'
import Component from 'vue-class-component'
import './components.css'

@Component
export default class InputView extends Vue {

	handleSearch(){
    var text = document.getElementById('homeInput').value
		this.$emit("search",text)
	}

  render () {
    return (
     <div>
    	<div class='SearchHeadViewClass'>
         <div style='width:100%;height:44px;display:flex'>
          
          <div style='flex:3'>
            <input id='homeInput' class='inputCSS' placeholder="输入记录编号" />
          </div>
          <span style='flex:1' class='SearchHeadViewRightIconClass' onClick = {this.handleSearch}>
          搜索
          </span>
        </div>
      </div>
      </div>
    )
  }
}




