import Vue from 'vue'
import Component from 'vue-class-component'
import './components.css'
import RecordListView from './recordList.js'
import {Watch} from 'decorators'
// import {SaveConfirmRecord,AddChangeRecord,Pay,PayConfirmRecord} from './methods.js'
import {SaveConfirmRecord,AddChangeRecord,Pay,PayConfirmRecord} from '@/module/methods.js'

@Component({
  props: {
    address:String,
    items: Array
  }
})

export default class DetailAlertView  extends Vue {
  orderId = ''
  orderStatus = ''
  status = 3
  lists = []
  yinshou = ''
  reTime =''
  num =''
  numCode = ''
  sender =''
  rece =''
  content = ''
  value = ''
  payer = ''
  receiveer =''
  myAddress = ''
  created() {
  	
  }

  @Watch('items', {immediate: true, deep: true})
  onItemsChange (items) {
    console.log('items111')
    if (items && items.length > 0) {
      console.log('items')
      console.log(items)

      this.lists = items
      // let obj =this.lists[this.lists.length - 1]
      let obj =this.lists[0]
      this.orderId = '编号' + obj.num
      this.numCode = obj.num
      let statusId = obj.status
      this.status = obj.status
      if (statusId == 1) {
      	this.orderStatus = '待收款方确认' 
      }else if (statusId == 2){
      	this.orderStatus = '待付款方支付'
      }else if (statusId == 3){
      	this.orderStatus = '待收款方确认'
      }else if (statusId == 4){
      	this.orderStatus = '已完结'
      }
      this.myAddress = this.address
      console.log(obj.payer)
      this.yinshou = '应收账款:' + obj.money + '元'
      this.reTime = '到期日期:' + obj.endtime
      this.num = '记录:' + obj.tiaoshu +'条'

      this.sender = '付款方:' + obj.payer
      console.log(this.sender)
      this.rece = '收款方:' + obj.receiveer


      this.payer = obj.payer;
      this.receiveer = obj.receiveer;
    }
  }

  createConent(value,index){
    
    var count = this.lists.length;
    count = count - index;
  	let statusId = value.status
    console.log("index:"+index+"count:"+count+"value:"+value);
  	var content=''
    if (statusId == 1) {
      content = count + "  " +this.formatDate(value.timestamp)+" 付款方"+value.payer+"创建记录，指定收款方为"+value.receiveer+",待收款方确认;"
    }else if(statusId ==2){
      if(count ==2){
        content = count + "  " +this.formatDate(value.timestamp)+" 收款方"+value.receiveer+"确认记录，记录生效;"
      }else{
        content = count + "  " +this.formatDate(value.timestamp)+" 收款方"+value.from+"变更记录，新收款方为"+value.receiveer+";"
      }
    }else if(statusId ==3){
      content = count + "  " +this.formatDate(value.timestamp)+" 付款方支付"+value.nasNum/1e18+"NAS给收款方"+value.receiveer+",待收款方确认;"
    }else if(statusId ==4){
      content = count + "  " +this.formatDate(value.timestamp)+" 收款方"+value.receiveer+"确认交易完结;"
    }
  	return content
  }

  handCreateList() {	
  	var item = this.lists.map((value,index) =>{
  		return <p>{this.createConent(value,index)}</p>
  	})
  	return item
  }

 formatDate (time) {
  let timeString = String(time)
  if (timeString.length < 13) {
    time = time * 1000
  }
  var date = new Date(time)
  var y = date.getFullYear()
  var m = date.getMonth() + 1
  m = m < 10 ? ('0' + m) : m
  var d = date.getDate()
  d = d < 10 ? ('0' + d) : d
  var h = date.getHours()
  h = h < 10 ? ('0' + h) : h
  var minute = date.getMinutes()
  var second = date.getSeconds()
  minute = minute < 10 ? ('0' + minute) : minute
  second = second < 10 ? ('0' + second) : second
  return y + '-' + m + '-' + d + ' ' + h + ':' + minute //+ ':' + second
  // return y + '-' + m + '-' + d + ' '
}

  handleClose () {
  	this.$emit('close')
  }

  handlePay(){
    Pay(this.value,this.numCode)
  }

  handleSaveConfirmRecord(){
    SaveConfirmRecord(this.numCode)
  }

  handleAddChangeRecord(){
    AddChangeRecord(this.value,this.numCode)
  }

  handlePayConfirmRecord(){
    PayConfirmRecord(this.numCode)
  }

  handleChange (val){
    this.value = val.target.value
  }

  handleBottomViewWithStatus() {
    this.myAddress = !this.myAddress? '':this.myAddress
    //付款方身份（管理页面输入的钱包地址与该编号的最后一条记录的payer相等）
    if (this.payer ==  this.myAddress) {
        if (this.status == 1) {
          //该交易待收款方确认后方可正式生效
          return <p style = 'margin:20px 0;text-align:center;color:#ff4545;font-size:10px;'>该交易待收款方确认后方可正式生效</p>
        }
        else if (this.status == 2) {
          ///立即支付
          return <div class = 'textBtnClass'>
            <p style = 'margin:10px 0;text-align:center;color:#ff4545;font-size:10px'>请输入与账款等值的NAS数量，支付成功后需要收款方进行完结确认操作</p>
            <input style='border:0.5px #222 solid;width:50%;background:#fff;float:none;margin:0 auto;display:block;margin-bottom:10px;color:#666' class='inputCSS' onChange={value => { this.handleChange(value) }} placeholder="输入等值的NAS数量" />
            <span onClick = {this.handlePay} style='background:#009688'>立即支付</span>
          </div>
        }else if (this.status == 3) {
        //已支付待确认
          return <p style = 'margin:20px 0;text-align:center;color:#ff4545;font-size:10px'>已支付，待收款方进行记录完结确认</p>
        }else if (this.status == 4) {
          return <p style = 'margin:20px 0;text-align:center;color:#ff4545;font-size:10px'>该记录已完结</p>
        }
    }
    //收款方身份（管理页面输入的钱包地址与该编号的最后一条记录的receiver相等）
    else if(this.receiveer == this.myAddress){
        if (this.status == 1) {
          //记录待确定
          return <div class = 'textBtnClass'>
            <p style = 'margin:10px 0;text-align:center;color:#ff4545;font-size:10px'>立即确认即代表该交易正式生效</p>
            <span onClick={this.handleSaveConfirmRecord}>立即确认</span>
          </div>
        }else if (this.status == 2) {
          //新增变更记录
          return <div class = 'textBtnClass'>
            <p style = 'margin:10px 0;text-align:center;color:#ff4545;font-size:10px'>变更记录是将全部账款转给新收款方</p>
            <input style='border:0.5px #222 solid;width:50%;background:#fff;float:none;margin:0 auto;display:block;margin-bottom:10px;color:#666' class='inputCSS' onChange={value => { this.handleChange(value) }} placeholder="输入新收款方的钱包地址" />
            <span onClick={this.handleAddChangeRecord} style='background:#009688;'>新增变更记录</span>
          </div>
        }else if (this.status == 3) {
          //立即确认
          return <div class = 'textBtnClass'>
            <p style = 'margin:10px 0;text-align:center;color:#ff4545;font-size:10px'>请确认收款地址是否已收到与账款等值的NAS，立即确认即表示该交易已完结</p>
            <span onClick={this.handlePayConfirmRecord}>立即确认</span>
          </div>
        }else if (this.status == 4) {
          return <p style = 'margin:20px 0;text-align:center;color:#ff4545;font-size:10px'>该记录已完结</p>
        }
    }
    //游客身份（首页通过编号查询的样式）
    else{
      if (this.status == 1) {
          //该交易待收款方确认后方可正式生效
          return <p style = 'margin:20px 0;text-align:center;color:#ff4545;font-size:10px'>该交易待收款方确认后方可正式生效</p>
      }else if (this.status == 2) {
        return <p style = 'margin:20px 0;text-align:center;color:#ff4545;font-size:10px'>等待收款方变更交易或付款方支付所有款项</p>
      }else if (this.status == 3) {
          return <p style = 'margin:20px 0;text-align:center;color:#ff4545;font-size:10px'>付款方已支付，待收款方进行记录完结确认</p>
      }else if (this.status == 4) {
          return <p style = 'margin:20px 0;text-align:center;color:#ff4545;font-size:10px'>该记录已完结</p>
      }
    }

  }

  render () {
    return (
     <div class = "alertClass">
     	<div class = 'alertHeadClass'>
     		<span onClick={this.handleClose}>╳</span>
     		<p>{this.orderId}</p>
     		<p style='text-align:right;margin-right:10px;color:#00998b;'>{this.orderStatus}</p>
     	</div>

     	<div class = 'alertInfoClass'>
     		<div class='infoOne'>
     			<p style='text-align:left'>{this.yinshou}</p>
     			<p>{this.reTime}</p>
     			<p>{this.num}</p>
     		</div>
     		<div class = 'infoTwo'>
     			<p style='text-align:left'>{this.sender}</p>
     			<p>{this.rece}</p>
     		</div>
     	</div>
     	<div class = 'historyClass'>
     		{this.handCreateList()}
     	</div>

     	<div>
     		{this.handleBottomViewWithStatus()}
     	</div>
      </div>
    )
  }
}


