import Vue from 'vue'
import Component from 'vue-class-component'
import './components.css'

@Component({
	props:{
		datas:Array
	}
})
export default class RecordListView extends Vue {
  
  handCreateList() {	
	var item = this.datas.map((value) =>{
		return <p>123123</p>
	})
	return item
  }

  render () {
    return (
     <div>
     	{this.handCreateList()}
     </div>
    )
  }
}


