var intervalQuery

function cbPush(resp) {
  if (isString(resp)) {
    alert("您已取消交易");
  }else{
    intervalQuery = setInterval(function() {
      funcIntervalQuery(resp.txhash);
    }, 5000);
  }
}

function funcIntervalQuery(hash, type) {
  axios({
      method: 'POST',
      url: "https://mainnet.nebulas.io/v1/user/getTransactionReceipt",
      data: JSON.stringify({
        hash: hash
      }),
    })
    .then(function(response) {
      console.log(response)
      if (response.data.result.status == 1) {
        clearInterval(intervalQuery);
        if (functionName === 'save') {
          alert('您已成功创建新记录');
          eventBus.$emit('addSuccess', otherAddress)
        }else if (functionName === 'SaveConfirmRecord'){
          alert('您已确认，该交易正式生效');
          eventBus.$emit('refreshAlert')
        }else if (functionName === 'PayConfirmRecord'){
          alert('您已确认，该交易已完结');
          eventBus.$emit('refreshAlert')
        }else if (functionName === 'Pay'){
          alert('您已成功支付'+payValue+'NAS');
          eventBus.$emit('refreshAlert')
        }else if (functionName === 'AddChangeRecord'){
          alert('您已成功变更记录');
          eventBus.$emit('refreshAlert')
        }

      } else if (response.data.result.status == 0) {
        alert(response.data.result.execute_result);
        clearInterval(intervalQuery);
      } else {
        console.log(response);
        return;
      }
    });
}

