import Vue from 'vue'
import Component from 'vue-class-component'
import './components.css'

@Component
export default class HeadView  extends Vue {
handleClickLeft() {
    this.$emit('backHome')
  }

  handleClickRight() {
    this.$emit('addRecord')
  }

  handleManagerRecord(){
  	    this.$emit('managerRecord')
  }
  render () {
    return (
     	<div class = "headViewClass">
     		
     		<dd onClick = {this.handleManagerRecord}>管理记录</dd>
     		<dd onClick = {this.handleClickRight}>新增记录</dd>
     		<dd onClick = {this.handleClickLeft}>首页</dd>

     	</div>
    )
  }
}


