export default [
  {
    path: '/HomePage',
    name: 'HomePage',
    meta: {
      fullscreen: true,
      keepAlive: true
    },
    component: resolve => require(['./index'], component => { resolve(component.default) })
  },
  {
    path: '/AddRecord',
    name: 'AddRecord',
    meta: {
      fullscreen: true,
      keepAlive: true
    },
    component: resolve => require(['./addRecord'], component => { resolve(component.default) })
  },
  {
    path: '/ManagerRecord/address',
    name: 'ManagerRecord',
    meta: {
      fullscreen: true,
      keepAlive: true
    },
    component: resolve => require(['./managerRecord'], component => { resolve(component.default) })
  }
]



